/* Слайдер */
.service-slider-container {
    width: 100%;
    background: linear-gradient(to right, rgba(226, 15, 15, 0.08) 0%, rgba(117, 141, 230, 0.08) 50%, rgba(88, 88, 89, 0.08) 100%);   
    padding: 20px;
}

.service-slider-track {
    display: flex;
    width: max-content;
    animation: scrollSlider 20s linear infinite;
}

.service-slide {
    flex-shrink: 0;
    width: 100px;
    margin: 0 20px;
    transition: transform 0.3s ease;
}

.service-slide img {
    width: 70%;
    height: auto;
    border-radius: 12px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.4);
}

.service-slide:hover {
    transform: scale(1.05);
}

@keyframes scrollSlider {
    0% {
        transform: translateX(0);
    }

    100% {
        transform: translateX(-50%);
    }
}