* {
    margin: 0;
    padding: 0;
}

html {
    scroll-behavior: smooth;
}

@font-face {
    font-family: "Steppe-Book";
    src: url("./fonts/Steppe-Book.ttf");
}

@font-face {
    font-family: "Steppe-ExtraBold";
    src: url("./fonts/Steppe-ExtraBold.ttf");
}

@font-face {
    font-family: "Roboto";
    src: url("./fonts/Roboto-Light.ttf");
}

body {
    background: linear-gradient(to right, #ea0808 0%, #1032ab 50%, #585859 100%);
    background-position: center;
    background-size: cover;
    font-family: 'Steppe-ExtraBold', sans-serif;
    color: #FFFFFF;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
}

.button {
    background-color: #ffffff;
    border-radius: 60px;
    width: 178px;
    height: 58px;
    box-sizing: border-box;
    text-decoration: none;
    padding: 18px;
    text-align: center;
    color: #5243F4;
    border: 0;
    outline: none;
    cursor: pointer;
}

.button:hover:not(:disabled) {
    background-color: #FFFFFF;
}

.button:disabled {
    cursor: not-allowed;
    color: grey;
    background-color: #343434;
}

.violet-button {
    background-color: #5243F4;
    color: white;
}

.button:hover {
    background-color: #eeeeee;
}

.button.violet-button:hover {
    background-color: #3b2cd8;
}

.button:hover {
    background-color: #FFFFFF;
}

.button.white-button {
    background-color: #FFFFFF;
}

.button.white-button:hover {
    background-color: #14bfe1;
}

.common-title {
    font-family: 'Steppe-ExtraBold', sans-serif;
    font-size: 60px;
    line-height: 100%;
    text-align: center;
    color: #fff;
}

.main {
    /* overflow: hidden; */
    position: relative;
    padding-bottom: 88px;
}

.currency {
    position: fixed;
    right: 50px;
    bottom: 50px;
    border: 1px solid rgb(150, 67, 67);
    padding: 5px 10px;
    height: 25px;
    min-width: 35px;
    border-radius: 60px;
    text-align: center;
    cursor: pointer;
    line-height: 28px;
    z-index: 1;
    color: #ffffff;
    user-select: none;
    transform: scale(1);
    animation: pulse 2s infinite;
}

@keyframes pulse {
    0% {
        transform: scale(0.9);
        box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.5);
    }

    70% {
        transform: scale(1);
        box-shadow: 0 0 0 10px rgba(255, 255, 255, 0);
    }

    100% {
        transform: scale(0.9);
        box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.5);
    }
}

.black-block {
    position: absolute;
    width: 477px;
    height: 1098px;
    top: 0;
    right: calc(50% - 780px);
    z-index: -2;

    background: linear-gradient(to right, rgba(226, 15, 15, 0.08) 0%, rgba(117, 141, 230, 0.08) 50%);
    /* backdrop-filter: blur(14px); */
    /* -webkit-backdrop-filter: blur(14px); */
    /* border: 1px solid rgba(255, 255, 255, 0.06);
    border-radius: 20px; */

    /* box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2); */
}

.header {
    padding: 46px 0;
}

.header .container {
    display: flex;
    align-items: center;
}

.logo img {
    max-width: 129px;
    vertical-align: bottom;
}

.menu {
    margin-left: 177px;
    margin-right: auto;
}

.menu ul {
    list-style: none;
    display: flex;
    gap: 65px;
}

.menu ul a {
    font-size: 14px;
    color: #FFFFFF;
    font-weight: bold;
    text-decoration: none;
    cursor: pointer;
    text-transform: uppercase;
}

.menu ul a:hover {
    color: #F8FF23;
}

.phone {
    font-size: 14px;
    color: #FFFFFF;
    font-weight: bold;
    text-decoration: none;
    cursor: pointer;
    letter-spacing: normal;
}

.main-content {
    padding-top: 61px;
    padding-bottom: 200px;    
}

.main-info {
    max-width: 811px;
}

.main-title {
    font-family: "Steppe-ExtraBold", sans-serif;
    color: #dce1e8;
    font-size: 70px;
    line-height: 100%;
    margin-bottom: 20px;
}

.main-text {
    color: #fff9f9;
    max-width: 697px;
    font-size: 24px;
    line-height: 150%;
    margin-bottom: 40px;
    font-family: "Roboto", sans-serif;
    font-weight: normal;
}

.main-image {
    position: absolute;
    top: 150px;
    right: -176px;
    z-index: -1;
    animation: breathe 6s ease-in-out infinite;
    transform-origin: center;
}

@keyframes breathe {

    0%,
    100% {
        transform: scale(1);
    }

    50% {
        transform: scale(1.03);
    }
}

.services {
    background: linear-gradient(to right, #290404 50%, #323c5f 50%, rgba(88, 88, 89, 0.08) 50%);
    padding: 150px 0;  
}

.services h2 {
    font-size: 75px;
    color: #dce1e8;
    font-weight: 600;
    text-align: center;
}

.services-content {
    display: flex;
    padding-top: 60px;
}

.services-filter {
    margin-right: 77px;
    align-self: flex-start;
    position: sticky;
    top: 20px;
}

.services-filter ul {
    display: flex;
    flex-direction: column;
    row-gap: 15px;
    list-style: none;
}

.services-filter ul li {
    width: 227px;
    padding: 16px;
    text-align: center;
    border: 1px solid #FFFFFF;
    font-size: 16px;
    color: #FFFFFF;
    box-sizing: border-box;
    cursor: pointer;
    outline: none;
}

.services-filter ul li.active,
.services-filter ul li:hover {
    color: #F8FF23;
    border-color: #F8FF23;
}

/* Блок информации об услугах */

.services-items {
    display: flex;
    flex-direction: column;
    row-gap: 40px;
}

.service {
    background-color: rgba(102, 211, 209, 0.696);
    padding: 20px;
    display: flex;
    min-width: 450px;
}

.service img {
    max-width: 468px;
}

.service-details {
    margin-left: 24px;
    display: flex;
    flex-direction: column;
}

.service-details h4 {
    font-size: 30px;
    line-height: 120%;
    margin-bottom: 20px;
    color: #dce1e8;
}

.service-details p {
    font-size: 16px;
    line-height: 120%;
    letter-spacing: normal;
    color: #f6f1f1;
    height: 100px;
}

.service-action {
    margin-top: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.service-price {
    font-family: "Steppe-ExtraBold", sans-serif;
    font-size: 24px;
    color: #6359d0;
    margin-bottom: 10px;
}

.service-action .button {
    width: 244px;
}

.service {
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    cursor: pointer;
}

.service:hover {
    transform: scale(1.05);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
}

.about-us-image {
    width: 100%;
    height: auto;
    overflow: hidden;
}

.bg-about-us {
    width: 100%;
    height: auto;
    display: block;
}

.about-us {
    padding: 220px;
    background: linear-gradient(to right, #ce6b6b 50%, #4890ce 50%, #585859 100%);      
    /* background-image: url('./images/bg-about-us.jpeg'); */
    background-position: center;   
    color: #fff;    
}

.about-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 80px;
}

.about-text {
    flex: 1 1 540px;
    max-width: 540px;  
}

.section-tag {
    display: flex;    
    font-size: 80px;
    font-weight: 800;
    letter-spacing: 2px;
    line-height: 120%;
    color: #9c0f0f;
    text-transform: none;    
    margin-bottom: 1rem;
    background: rgba(177, 67, 67, 0.08);
    padding: 4px 12px;
    border-radius: 8px;
}

.about-text h1 {
    font-size: 46px;
    line-height: 1.3;
    font-weight: 800;
    margin-bottom: 20px;
}

.about-text .subtitle {
    display: block;
    font-size: 30px;
    color: #f0f0f0;
    margin-top: 10px;
}

.about-text p {
    font-size: 21px;
    line-height: 1.6;
    margin-bottom: 16px;
}

.about-image {
    flex: 1 1 400px;
    max-width: 480px;
    text-align: center;
}

.about-image img {
    width: 100%;
    height: auto;
    object-fit: contain;
    transition: transform 0.2s ease;
    will-change: transform;
    animation: float 4s ease-in-out infinite;
    display: block;
}

@keyframes float {
    0% {
        transform: translateY(0px) rotate(1deg);
    }

    50% {
        transform: translateY(-10px) rotate(-1deg);
    }

    100% {
        transform: translateY(0px) rotate(1deg);
    }
} 

/* Отзывы */

.client-reviews {
    font-family: 'Steppe-ExtraBold', sans-serif;
    font-size: 32px;
    margin-top: 80px;
    margin-bottom: 40px;
    color: #dce1e8;
}

.client-reviews h2 {
    font-size: 42px;
    font-weight: 700;
    margin-top: 40px;
    margin-bottom: 60px;
    text-align: center;
    font-family: 'Steppe-ExtraBold', sans-serif;
}

.reviews-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 32px;
}

.review-card {
    background-color: rgba(255, 255, 255, 0.08);
    border-radius: 12px;
    padding: 24px;
    width: 300px;
    text-align: center;
    backdrop-filter: blur(6px);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    color: #fff;
}

.review-card img {
    width: 100%;
    height: 180px;
    object-fit: cover;
    border-radius: 8px;
    margin-bottom: 16px;

}

.review-card h3 {
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 8px;
}

.review-card p {
    font-size: 16px;
    line-height: 1.4;
}

/* Блок с формой для записи на ТО */

.sign-up {
    overflow: hidden;
    padding-top: 189px;
    padding-bottom: 40px;
}

.sign-up .container {
    position: relative;
    padding-bottom: 121px;
}

.sign-up .sub-title {
    font-weight: bold;
    font-size: 65px;
    line-height: 100%;
    margin-bottom: 40px;
    color: #dce1e8;
}

.sign-up-text {
    font-size: 24px;
    line-height: 120%;
    color: #d4d4d4;
    margin-bottom: 60px;
    max-width: 555px;
}

.sign-up-form {
    max-width: 544px;
    box-sizing: border-box;
}

.sign-up-input {
    border: 1px solid white;
    border-radius: 60px;
    margin-bottom: 16px;
    padding: 24px 22px;
    font-size: 18px;
    width: 538px;
    height: 58px;
    background: transparent;
    box-sizing: border-box;
    outline: none;
    color: white;
}

.sign-up-input::placeholder {
    color: #ffffff;
}

.sign-up .button {
    width: 100%;
}

.sign-up-image {
    position: absolute;
    bottom: 0;
    left: 601px;
}

.footer {
    padding: 47px 0;
    border-top: 1px solid #5f5f5f;
}

.footer .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.rights {
    font-size: 14px;
    line-height: 90%;
    letter-spacing: normal;
    font-weight: bold;
    text-transform: uppercase;
    color: #f6f1f1;
}